<template>
	<div class="login">
		<el-form class="form" :model="loginFrom" status-icon :rules="rules" ref="loginFrom">
			<el-form-item prop="userName">
				<el-input type="text" v-model="loginFrom.userName" autocomplete="off" placeholder="用户名">
					<template slot="prepend"><i class="el-icon-user"></i></template>
				</el-input>
			</el-form-item>
			<el-form-item prop="password">
				<el-input type="password" v-model="loginFrom.password" autocomplete="off" placeholder="密码">
					<template slot="prepend"><i class="el-icon-key"></i></template>
				</el-input>
			</el-form-item>
			<el-form-item>
				<el-button type="primary" @click="submitForm('loginFrom')">登录</el-button>
				<el-button @click="resetForm('loginFrom')">重置</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
import { loginApi } from '@/api/user';
import router from '@/router';

export default {
	name: 'login',
	data() {
		var validateUserName = (rule, value, callback) => {
			if (value === '') {
				callback(new Error('请输入用户名'));
			} else {
				if (/^[\u4e00-\u9fa5]{2,12}$/.test(value)) {
					callback();
				} else {
					callback(new Error('只能包含2-12位汉字'));
				}
			}
		};
		var validatePassword = (rule, value, callback) => {
			if (value === '') {
				callback(new Error('请输入密码'));
			} else {
				if (/^\w{6,12}$/.test(value)) {
					callback();
				} else {
					callback(new Error('只能包含数字、字母、下划线6-12位'));
				}
			}
		};
		return {
			loginFrom: {
				userName: '',
				password: '',
			},
			rules: {
				userName: [{ validator: validateUserName, trigger: 'blur' }],
				password: [{ validator: validatePassword, trigger: 'blur' }],
			},
		};
	},
	deactivated() {
		this.resetForm('loginFrom');
	},
	methods: {
		submitForm(formName) {
			this.$refs[formName].validate(async valid => {
				if (valid) {
					this.$store.dispatch('login', this.loginFrom);
					// let res = await loginApi(this.loginFrom);
					// if (res.code == 0) {
					//     this.$message.warning('用户名或密码错误');
					// } else {
					//     this.$message.success('登录成功');
					//     this.$store.commit('SET_TOKEN', res.token);
					//     this.$store.commit('SET_USER', {userName:res.userName, userId: res.userId});
					//     // 如果存在redirect参数，登录后重定向到指定页面，否则回到首页
					//     let path = router.currentRoute.query.redirect;
					//     if (path) {
					//         router.replace(path);
					//     } else {
					//         router.replace('/');
					//     }
					//     // 登录成功获取购物车列表
					//     this.$store.dispatch('getCartList');
					// }
				} else {
					this.$message.warning('账号或者密码有误');
					return false;
				}
			});
		},
		resetForm(formName) {
			this.$refs[formName].resetFields();
		},
	},
};
</script>

<style scoped>
.login {
	margin-top: 100px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}
.login >>> .form {
	width: 30%;
}
</style>
